    };
  },
  // watch: {
  //   currentPage(oldV, newV) {
  //     console.log(oldV, newV);
  //   }
  // },
  methods: {
    // currentChange() {
    //   this.listFirst = (this.currentPage[1] - 1) * 5;
    //   console.log(this.listFirst);
    // },
    selectItem(path, data) {
      let item = JSON.stringify(data);
      this.$emit("select", path, item);
    }, //itemselect
    // routerLink(path) {
    //   this.$router.push(path); //
    // },
    tabLink(index, tocpn) {
      //
      this.$set(
        this.tabs,
        this.tabs[index],
        (this.tabs[index].isActive = true)
      );
      for (var i = 0; i < this.tabs.length; i++) {
        if (this.tabs[i].belong == this.tabs[index].belong && i != index) {
          this.$set(this.tabs, this.tabs[i], (this.tabs[i].isActive = false)); //methodsdata
        }
      }
      var t = 6;
      if (this.tabs[index].belong == "") t = 6;
      else if (this.tabs[index].belong == "") t = 7;

      this.currentTab[t] = this.tabs[index].tocpn;
      //
    },
    init() {
      var a = 0,
        b = 0;
      var url = this.HOST + "/conference/getRelevantConference";
      //
      this.axios
        .get(url, {
          params: {
            relevantId: 0
          }
        })
        .then(response => {
          console.log(response.data.result);
          //
          this.tabMessage[1] = response.data.message;
          this.total[0] = this.tabMessage[1].length;
          a = 1;
          this.isRequested = true; //
        })
        .catch(function(response) {
          console.log(response);
        });
      //
      this.axios
        .get(url, {
          params: {
            relevantId: -1
          }
        })
        .then(response => {
          console.log(response.data.result);
          //
          this.tabMessage[2] = response.data.message;
          this.total[1] = this.tabMessage[2].length;
          b = 1;
        })
        .catch(function(response) {
          console.log(response);
        });
      if (a && b) {
        this.isRequested = true; //
      }
    }
  },
  components: { NormalList, MeetingDynamic, MeetingItem }
};
</script>

<style lang="scss" scoped>
@mixin left {
  margin: 5px auto;
  width: 795px;
  float: left;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin right {
  margin: 5px auto;
  width: 390px;
  float: right;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin i-box {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 0.95);
}
.active {
  color: #127de7 !important;
  border-bottom: 2px solid #127de7;
  padding-bottom: 4px;
}
#exhibition-default {
  margin: 5px auto;
  width: 1200px;
  overflow: hidden;
}
.left_block {
  @include left;
}
.right_block {
  @include right;
}
.box1,
.box2,
.box3 {
  @include left;
  @include i-box;
}
.box3 {
  margin-bottom: 30px;
}
.box1 {
  & {
    color: #404040;
    font-weight: 500;
    padding: 0 15px;
    overflow: hidden;
  }
  .curr_position {
    & {
      margin-top: 14px;
      margin-bottom: 15px;
    }
    a {
      text-decoration: none;
      cursor: pointer;
    }
  }
}
.box4 {
  @include right;
  @include i-box;
}
.title {
  margin-bottom: 12px;
  margin-top: 2px;
  width: 100%;
  height: 28px;
  line-height: 28px;
}
.title li {
  float: left;
}
.more {
  & {
    color: #666;
    font-size: 14px;
    float: right;
    padding: 2px 10px 0 2px;
    cursor: pointer;
  }
  &:hover {
    color: #127de7;
  }
}

.list_title a {
  color: #7b7b7b;
  cursor: default;
  font-size: 1em;
  font-weight: 500;
  line-height: 25px;
  vertical-align: middle;
  margin-right: 10px;
}
.list_title a:hover {
  color: #127de7;
  border-bottom: 2px solid #127de7;
  cursor: default;
  padding-bottom: 4px;
}
</style>



